body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.list{
    color: black;
    -webkit-text-decoration: auto;
            text-decoration: auto;
    font-family: unset;
}
.logo{
    width: 90px;
}
.listaNav{
    display: flex;
    flex-direction: row;
    width: 16%;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 0;
}
.login{
    align-self: center;
    background-color: black;
    color: white;
}

.carritoCant{
    display: flex;
    align-items: center;
}

.cantCarrito{
    text-decoration: none;
    background: #e97171;
    border-radius: 50%;
    padding: 5px;
}
.saludo{
    display: flex;
    justify-content: space-around;
}


img{
    width: 185px;
}
.cajaProducto{
    display: grid;
    grid-template-columns: auto auto auto;
    justify-items: center;
}

.producto{
    margin: 15px;
}
.cardDetalle{
    text-align: center;
    border: 1px solid;
    width: 25%;
    box-shadow: 4px 5px rgba(145, 105, 105, 0.20);
    padding: 2%;
    margin-bottom: 3%;
}
.cardContainer{    
    text-align: -webkit-center;
    margin-top: 45px;
}
.cardContainer{
    margin-bottom: 20px;
}
.sumaRestaProd{
    display: flex;
    place-content: center;
    align-items: center;
}

.cantidadProductos{
    padding: 10px;
}
.listaCarrito{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.img-carrito{
    width: 70px;
}

.btnDeleteProduct{
    cursor: pointer;
}

.cartContainer{
    text-align: center;
}

.formularioCompra{
    display: grid;
    grid-template-columns: 30%;
    justify-content: center;
    padding: 30px;
    text-align: left;
}

.formulario{
    text-align: center;
}

.labelForm{
    margin-bottom: 10px;
}

input{
    height: 25px;
    margin-bottom: 10px;
}

.btnPagar{
    width: 22%;
    justify-self: center;
    margin-top: 15px;
}

#inputMail{
    margin-left: 31px;
}

h1{
    margin-bottom: 0;
    text-align: center;
}

button:disabled{
    cursor: not-allowed;
}
